import React, { useEffect, useState } from "react";
import 'css/common.css';
import { useHistory } from "react-router";
import CSRFToken from "components/csrftoken";
import axios from "axios";

function Signup({user, handleLogout, isAuthenticated}){
    const [signup, setSignup] = useState({ nickname: "", age: "0"})
    const [sex, setSex] = useState(0);
    const history = useHistory();

    const resign = async () =>{
        let res = await fetch('https://23cluster.com/user/resign/', {
            headers: {
              Authorization : `JWT ${localStorage.getItem('token')}`
            }
        });
        if (res.ok)
        {
            handleLogout();
            alert("   ..");
        }
        else{
            alert("  .\n .");
        }
    }
    const handleSubmit = async (event) => {
        event.preventDefault();
        if (signup.nickname === "")
        {
            alert(" .");
            return ;
        }
        if(signup.age === 0){
            alert("  ");
            return ;
        }
        if(sex === 0){
            alert("  ");
            return ;
        }
        var csrftoken = CSRFToken();
        const config = {
            headers: {
                'Authorization' : `JWT ${localStorage.getItem('token')}`
            }
        }
        await axios.post('https://23cluster.com/user/signup/', {
            nickname : signup.nickname,
            age : signup.age,
            sex : sex,
            csrfmiddlewaretoken	: csrftoken
        }, config).then((response) => {
          if (response.status === 200)
          { 
            history.push('/');
            history.go(0);
          }
          else
          {
            alert("   .");
            resign();
            history.push('/');
            history.go(0);
          }
        })
        .catch((error) => {
        //  
          resign();
          console.log(error);
        })
      }
    
    const handleChange = (event) => {
        const { name, value } = event.target;
        setSignup({...signup, [name] : parseInt(value)});
    }
    const handleRadio = (event) =>{
        const { value } = event.target;
        setSex(parseInt(value));
    }
    const handleNickname = (event) => {
        let { name, value } = event.target;
        if (value.length > 7)
            value = value.substr(0, 7);
        setSignup({...signup, [name] : value});
      };
	return (
		<>
		<div className="body-wrap">
            <form onSubmit={handleSubmit}>
                 : <input type="text" name="nickname" placeholder=" " onChange={handleNickname} value={signup.nickname} maxLength={7}/><br/>
                 : <input type="range" name="age" min="16" max="45" onChange={handleChange} value={signup.age}/>{signup.age}<br/>
                 : <input type="radio" value="1" name="sex" onChange={handleRadio} checked={sex === 1 ? true : false}/><label htmlFor="1"></label> <input type="radio" value="2" name="sex" onChange={handleRadio} checked={sex === 2 ? true : false}/><label htmlFor="2"></label>
                <input type="submit" value=""/>
            </form>
		</div>
		</>
	)
}

export default Signup;