import React, { useEffect, useState } from "react";
import AppRouter from "components/Router";
//import NavBar from "./components/routing/Nav";
//import Routes from "./Routes";

function App() {
	const [user, setUser] = useState([]);
	let [isAuthenticated, setisAuthenticated] = useState(localStorage.getItem('token') ? true : false);

	const asyncLocalStorage = {
		setItem: function (key, value) {
			return Promise.resolve().then(function () {
				localStorage.setItem(key, value);
			});
		},
		getItem: function (key) {
			return Promise.resolve().then(function () {
				return localStorage.getItem(key);
			});
		}
	};
	
	const userHasAuthenticated = (authenticated, username, token) => { 
	  asyncLocalStorage.setItem('token', token);
      setUser(username)
	  setisAuthenticated(authenticated)
	}//     
  
	const handleLogout = () => {
		setisAuthenticated(false)
		setUser("")
		localStorage.removeItem('token');
		window.location.href = '/';
	}//

	useEffect(() => {
	  // (access token)     GET /validate   access token  
	  
	  async function init(){
	  setUser({
		user_comment_like : [],
		user_comment_unlike : [],
		user_post_like : [],
		user_post_unlike : [],	
	  });
	  if (isAuthenticated) {
		//  JWT    GET /validate   
		//   200  GET /user/current   user 
		const res = await fetch('https://23cluster.com/user/verify/', {
		  method: 'POST',
		  headers: {
			'Content-Type': 'application/json'
		  },
		  body: JSON.stringify({
			token: localStorage.getItem('token')
		  })
		});
		if (!res.ok)
		{
			handleLogout();
			window.location.href('/');
		}
		let res1 = await fetch('https://23cluster.com/user/current/', {
			headers: {
			  Authorization : localStorage.getItem('token')
			}
		  });
		if (!res1.ok)
    	  handleLogout();
		let json = await res1.json();
		//    ,   state  
		if (json.username) {
		  //include  array   __proto__ f   .
		  setUser(json);
		  setisAuthenticated(true);
		}else{
		  // undefined   modal false  
		  setisAuthenticated(false);
		}
		// Refresh Token   token   
		let res2 = await fetch('https://23cluster.com/user/refresh/', {
		  method: 'POST',
		  headers: {
			'Content-Type': 'application/json'
		  },
		  body: JSON.stringify({
			token : localStorage.getItem('token')
		  })
		})
		let json2 = await res2.json();
		localStorage.setItem('token', json2.token);
		//  userHasAuthenticated(true, json.user.username, json.token);
	  }
	}
	init();
	},[isAuthenticated])

  return (
    <>
      <AppRouter isAuthenticated={isAuthenticated} user={user} userHasAuthenticated={userHasAuthenticated} 
	  handleLogout={handleLogout} />
    </>
  );
}

export default App;
