import React, { useState } from 'react';
import { useHistory } from 'react-router'
import GoogleLogin from 'react-google-login';
import KakaoLogin from 'react-kakao-login';
import "../css/login.css";

function LoginModal(props){
  const history = useHistory()
  const [modal, setModal] = useState(false);

  //     
  let responseGoogle = async (res)=>{
    let email = res.profileObj.email
    let id_token = res.profileObj.googleId
    let data = {
      username: id_token,
      password: email,
      provider: 'google'
    }

    await fetch('https://23cluster.com/user/', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json'
      },
      body: JSON.stringify(data)
    })
    .then(res => res.json())
    .then(async json => {
      if (json.username && json.token) {
		await props.userHasAuthenticated(true, json.username, json.token);
        history.push('/signup');
        return ;
      }else{
        //  Google     Login  
        //       access token  
        fetch('https://23cluster.com/user/login/', {  
        method: 'POST',
          headers: {
            'Content-Type': 'application/json'
          },
          body: JSON.stringify(data)
        })
        .then(res => res.json())
        .then(async json => {
          //       Local Storage 
          if (json.user && json.user.username && json.token) {
            await props.userHasAuthenticated(true, json.user.username, json.token);
            history.goBack();
          }
        })
        .catch(error => {
          console.log(error);
          window.gapi && window.gapi.auth2.getAuthInstance().signOut();
        });   
      }
    })
    .catch(error => {
      console.log(error);
      window.gapi && window.gapi.auth2.getAuthInstance().signOut();
    });
  }//     

  let responseKakao = (res)=>{
    let nickname = res.profile.kakao_account.profile.nickname
    let id_token = res.profile.id
    let data = {
      username: id_token,
      password: nickname,
      provider: 'kakao'
    }

    fetch('https://23cluster.com/user/', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json'
      },
      body: JSON.stringify(data)
    })
    .then(res => res.json())
    .then(async json => {
      if (json.username && json.token) {
        await props.userHasAuthenticated(true, json.username, json.token);
        history.push('/signup');
        return ;
      }else{
        //  Google     Login  
        //       access token  
        fetch('https://23cluster.com/user/login/', {  
        method: 'POST',
          headers: {
            'Content-Type': 'application/json'
          },
          body: JSON.stringify(data)
        })
        .then(res => res.json())
        .then(async json => {
          //       Local Storage 
          if (json.user && json.user.username && json.token) {
            await props.userHasAuthenticated(true, json.user.username, json.token);
            history.goBack();
          }
        })
        .catch(error => {
          console.log(error);
          window.kapi && window.kapi.auth2.getAuthInstance().signOut();
        });   
      }
    })
    .catch(error => {
      console.log(error);
      window.kapi && window.kapi.auth2.getAuthInstance().signOut();
    });  
  }//     

  const responseFacebook = () =>{
	  alert(" ");
  }

  return(
    <>
	<div className="body">
		<div className="box">
			<p className="login"></p>

			<div>
				<KakaoLogin
				buttonText=" "
				jsKey={'8bddd75ad7eb715e2ef60468da308305'}
				onSuccess={responseKakao}
				onFailure={responseKakao}
				getProfile={true}
				className="yellow_kakao"
				/>	
			</div>
			<div>
				<a className="blue" onClick={responseFacebook}>
					<div className="facebook"></div>
					<div className="facebooktext">
						<p> </p>
					</div>
				</a>
			</div>
			<div>
				<GoogleLogin
						render={renderProps => (
							<div>
							<div className="gray" onClick={renderProps.onClick}>
								<div className="google"></div>
								<div className="googletext">
									
								</div>
							</div>
							</div>
						)}
						clientId="993167427573-hbdhu576tcrqmhljcp8tfjbohtfu5li7.apps.googleusercontent.com"
						onSuccess={responseGoogle}
						onFailure={responseGoogle}
						cookiePolicy={'single_host_origin'}
						></GoogleLogin>
			</div>
		</div>
	</div>

    </>
  )
}


export default LoginModal;