import React, {useState, useEffect} from "react";
import "@toast-ui/editor/dist/toastui-editor.css";
import { Editor } from "@toast-ui/react-editor";
import axios from "axios";
import 'css/input.css';
import CSRFToken from "../components/csrftoken.js";
import {useHistory} from "react-router-dom";

const DraftEditor = ({user, handleLogout}) => {
  const editorRef = React.createRef();
  const [postTitle , setPostTitle] = useState("");
  const [section , setSection] = useState("1");
  const [hashtag, setHashtag] = useState("");
  const history = useHistory();

//  const handleClick = (event) => {
//	event.preventDefault();
//    setContent({
//      content: editorRef.current.getInstance().getMarkdown(),
//    });
//  };

  const onSubmit = async (event) => {
    event.preventDefault();
	if (postTitle === "")
	{
		alert(" ");
		return ;
	}
	if(editorRef.current.getInstance().getMarkdown() === ""){
		alert(" ");
		return ;
	}
	var csrftoken = CSRFToken();
	const config = {
		headers: {
			'Authorization' : `JWT ${localStorage.getItem('token')}`	
		}
	}
    await axios.post('https://23cluster.com/api/post/add/', {
        title: postTitle,
        content: editorRef.current.getInstance().getMarkdown(),
        writer_id: user.user_pk,
        writer_name: user.username,
		writer_category:user.category,
		section : section,
		hashtag : hashtag,
		csrfmiddlewaretoken	: csrftoken
    }, config).then((response) => {
	  history.push('/');
    })
    .catch((error) => {
    //  
      alert(" .");
      handleLogout();
      console.log(error);
	})
  }

  const onChangeTitle = (event) => {
    const {
      target: { value },
      
    } = event;
    setPostTitle(value);
  }

  const onChangeHashtag = (event) => {
    const {
      target: { value },
    } = event;
    setHashtag(value);
  }

  const onChangeSection = (event) => {
    const {
      target: { value },
    } = event;
    setSection(value);
	console.log(value);
  }

  const uploadImage = (blob) => {
	let formData = new FormData();
    formData.append('image', blob, blob.name);
    formData.append('image_name', blob.name);
    let csrftoken = CSRFToken();
    return axios('https://23cluster.com/api/post/upload_image/', { //fetch ?  axios 
        method: 'POST',
		headers: {
			'Content-Type' : 'multipart/form-data' ,
			'Authorization' : `JWT ${localStorage.getItem('token')}`
		},
        data: formData,
    })
    .then(res => {
        return (res);
    });
  }

  const onAddImageBlob = (blob, callback) => {
    uploadImage(blob)
        .then(response => {
            console.log(response);
            callback(`https://23cluster.s3.ap-northeast-2.amazonaws.com/${response.data.url}`, 'alt text');
        }).catch(error => {
            console.log(error);
        });
};

  return (
    <>
    <div className="editor">
		<h1 style={{"margin-top" : "15px"}} className="input_title"></h1>
		<div style={{"margin-left" : "10px"}}>
			<div className="inputbox">
				<p></p>
				<input className="textbox1" type="text" minlength="5" value={postTitle}
					placeholder="       ." name="title" onChange={onChangeTitle}
					maxLength={40}/>
			</div>
			<div className="inputbox">
				<p>	</p>
				<div className="category_box">
				<select name="section" onChange={onChangeSection} value={section} >
					<option value="1"></option>
					<option value="2"></option>
					<option value="3"></option>
					<option value="4"></option>
					<option value="5"></option>
				</select>
			</div>
		</div>
		<div className="inputbox">
			<p></p>
			<input className="textbox1" type="text" minlength="5" value={hashtag}
				placeholder="  3 #" name="title" onChange={onChangeHashtag}
				maxLength={40}/>
		</div>
		</div>
	  <br/><br/>
      <Editor
        previewStyle="vertical"
        height="300px"
        initialEditType="wysiwyg"
        placeholder=""
		name="content"
        hideModeSwitch={true}
        ref={editorRef}
		hooks={{
			addImageBlobHook: onAddImageBlob
		}}
      />
	  <span onClick={onSubmit}></span>
    </div>
    </>
  );
};
export default DraftEditor;